body{
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  background-image: url('./images/bg.jpg')

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navColor{
background-color: #F2A580;
font-size: 20px;
}

.wordCount{
 
  color: #E9692C;
  
}

.mainHead{
  color: #F2A580;
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
  text-shadow: 2px 2px #E9692C;
}
.btnColor{
  background-color: #F2A580;
  outline: none;
 border: none;
 border-radius: 7px;
  padding: 8px;
  margin: 5px;
  transition: transform 500ms ease-in-out 25ms;
}
.btnColor:hover{
  background-color: #E9692C;
  transform: scale(1.15);
  outline: none;
  border: none;
}
.btnColor:focus{
 
  outline: none;
  border: none;
}

.cont{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.textareaColor:focus{
  outline: none;
  border: none;
  box-shadow: 1px 1px 5px 3px #E9692C;
}
.textareaColor:hover{
  outline: none;
  border: none;
  box-shadow: 1px 1px 5px 3px #E9692C;
}